import axios from 'axios';
const API_KEY = '23976029dbf9e65a92e47a5047de0abf';
const BASE_URL = 'https://api.themoviedb.org/3/';

// /trending/get-trending ÑÐ¿Ð¸ÑÐ¾Ðº Ð½Ð°Ð¹Ð¿Ð¾Ð¿ÑƒÐ»ÑÑ€Ð½Ñ–ÑˆÐ¸Ñ… Ñ„Ñ–Ð»ÑŒÐ¼Ñ–Ð² Ð½Ð° ÑÑŒÐ¾Ð³Ð¾Ð´Ð½Ñ– Ð´Ð»Ñ ÑÑ‚Ð²Ð¾Ñ€ÐµÐ½Ð½Ñ ÐºÐ¾Ð»ÐµÐºÑ†Ñ–Ñ— Ð½Ð° Ð³Ð¾Ð»Ð¾Ð²Ð½Ñ–Ð¹ ÑÑ‚Ð¾Ñ€Ñ–Ð½Ñ†Ñ–.

const getTrendingMovies = async () => {
  return await axios
    .get(`${BASE_URL}trending/movie/day?api_key=${API_KEY}`)
    .then(response => {
      return response.data.results.map(({ title, id }) => ({ title, id }));
    });
};
// /search/search-movies Ð¿Ð¾ÑˆÑƒÐº Ñ„Ñ–Ð»ÑŒÐ¼Ñƒ Ð·Ð° ÐºÐ»ÑŽÑ‡Ð¾Ð²Ð¸Ð¼ ÑÐ»Ð¾Ð²Ð¾Ð¼ Ð½Ð° ÑÑ‚Ð¾Ñ€Ñ–Ð½Ñ†Ñ– Ñ„Ñ–Ð»ÑŒÐ¼Ñ–Ð².
const getSearchedMovie = async query => {
  return await axios
    .get(`${BASE_URL}search/movie?query=${query}&api_key=${API_KEY}`)
    .then(response => {
      return response.data.results;
    });
};
// /movies/get-movie-details Ð·Ð°Ð¿Ð¸Ñ‚ Ð¿Ð¾Ð²Ð½Ð¾Ñ— Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ— Ð¿Ñ€Ð¾ Ñ„Ñ–Ð»ÑŒÐ¼ Ð´Ð»Ñ ÑÑ‚Ð¾Ñ€Ñ–Ð½ÐºÐ¸ ÐºÑ–Ð½Ð¾Ñ„Ñ–Ð»ÑŒÐ¼Ñƒ.

const getMovieDetails = async movieId => {
  return await axios
    .get(`${BASE_URL}movie/${movieId}?api_key=${API_KEY}`)
    .then(res => res.data)
    .then(({ title, poster_path, vote_average, overview, genres }) => ({
      title,
      poster: poster_path
        ? `https://image.tmdb.org/t/p/w300${poster_path}`
        : 'https://www.ormistonhospital.co.nz/wp-content/uploads/2016/05/No-Image.jpg',
      vote_average,
      overview,
      genres,
    }));
};

// /movies/get-movie-credits Ð·Ð°Ð¿Ð¸Ñ‚ Ñ–Ð½Ñ„Ð¾Ñ€Ð¼Ð°Ñ†Ñ–Ñ— Ð¿Ñ€Ð¾ Ð°ÐºÑ‚Ð¾Ñ€ÑÑŒÐºÐ¸Ð¹ ÑÐºÐ»Ð°Ð´ Ð´Ð»Ñ ÑÑ‚Ð¾Ñ€Ñ–Ð½ÐºÐ¸ ÐºÑ–Ð½Ð¾Ñ„Ñ–Ð»ÑŒÐ¼Ñƒ.

const getMovieCredits = async movieId => {
  return await axios
    .get(
      `${BASE_URL}movie/${movieId}/credits?api_key=${API_KEY}&language=en-US`
    )
    .then(response => {
      return response.data.cast.map(
        ({ credit_id, character, name, profile_path }) => ({
          credit_id,
          name,
          character,
          profile: profile_path
            ? `https://image.tmdb.org/t/p/w300${profile_path}`
            : 'https://www.ormistonhospital.co.nz/wp-content/uploads/2016/05/No-Image.jpg',
        })
      );
    });
};

// /movies/get-movie-reviews Ð·Ð°Ð¿Ð¸Ñ‚ Ð¾Ð³Ð»ÑÐ´Ñ–Ð² Ð´Ð»Ñ ÑÑ‚Ð¾Ñ€Ñ–Ð½ÐºÐ¸ ÐºÑ–Ð½Ð¾Ñ„Ñ–Ð»ÑŒÐ¼Ñƒ.

const getMovieReviews = async movieId => {
  return await axios
    .get(
      `${BASE_URL}movie/${movieId}/reviews?api_key=${API_KEY}&language=en-US`
    )
    .then(response => {
      return response.data.results.map(({ id, author, content }) => ({
        id,
        author,
        content,
      }));
    });
};
const api = {
  getTrendingMovies,
  getSearchedMovie,
  getMovieDetails,
  getMovieCredits,
  getMovieReviews,
};
export default api;
